* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  /* display: block;
  max-width: 70%;
  height: auto; */
  width: 100%;
  height: 165px;
/* height: 150px; */
  object-fit: cover;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 8px;
  margin-bottom: 24px;
}

.load-more {
  /* display: block;
  margin-left: auto;
  margin-right: auto; */

  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;
  padding: 8px 24px;
  border-radius: 8px;
  border: 0;
  color: #fafafa;
  background-color: navy;
  cursor: pointer;
}

.load-more.is-hidden {
  display: none;
}

